import React, { useState, useEffect, useContext } from "react";

import "./CovidScreen.css";
import {
  MenuItem,
  FormControl,
  Select,
  Card,
  CardContent,
} from "@material-ui/core";

import {
  districForMap, HeaderData
} from "../context/headerContext";
import { ListSelect } from "../static/own-comp";
import {
  convertToCommune,
  convertToDistrict,
  convertToVillage,
  totalArray
} from "../function/fn";
import InfoBox from "../component/covideComponents/InfoBox";

import Table from "../component/covideComponents/Table";
import { sortData, prettyPrintStat } from "../component/covideComponents/util";
import numeral from "numeral";
import Map from "../component/covideComponents/Map";
import { joinArray } from "../component/covideComponents/util";
import "leaflet/dist/leaflet.css";
import { Form, Divider, Popconfirm, Row, Col, Button } from "antd";
import {
  GET_ALL_PROVINCE,
  GET_DATA_FOR_MAP,
  GET_ALL_DATA,
} from "../graphql/dashboardAndReport";
import {
  GET_REPORT_DATA_ADDITIONAL,
  GET_REPORT_DATA_ADDITIONALTWO
} from '../graphql/report'
import { GET_SUM_SAMPLE } from "../graphql/sample";
import { useQuery } from "@apollo/client";
import BarChart from "../component/covideComponents/BarChart";
import ImageCovid from '../asset/covid19.png'
import ImageRecover from '../asset/recover.png'
import ImageDeath from '../asset/death.png'
import ImageCure from '../asset/curring.png'
import ImageDirect from '../asset/direct.png'
import ImageClose from '../asset/close-area.png'
import ImageNotDirect from '../asset/notdirect.png'
import ImageOpen from '../asset/open-area.png'
import LineGraph from "../component/covideComponents/LineGraph";
import BarGraph from '../component/covideComponents/BarGraph'
import AddMoreData from '../component/map/modal/addMoreData'
import LineChartTesting from "../component/covideComponents/LineChartTesting";
import { GET_ROLES } from '../graphql/auth'
import { sum } from "lodash";
import { useHistory } from "react-router-dom";
import LoadingOverlay from "react-loading-overlay";

const MapScreen = () => {
  let history = useHistory()

  const { user } = useContext(HeaderData)

  let roles = user?.roles?.map(e => e.role)
  let pages = user?.pages?.map(e => e.page)

  const [openModal, setOpenModal] = useState(false)

  const districtLatLong = [
    {
      district: "",
      lat: 13.60472222544493,
      long: 103.45245485053685,
    },
    {
      district: "",
      lat: 13.867238663711532,
      long: 103.87715707241854,
    },
    {
      district: "",
      lat: 13.852238272312373,
      long: 103.53639730433542,
    },
    {
      district: "",
      lat: 13.713723851088394,
      long: 103.67219336284299,
    },
    {
      district: "",
      lat: 13.605041870228655,
      long: 103.97859822744759,
    },
    {
      district: "",
      lat: 13.414442053292065,
      long: 104.10460200711728,
    },
    {
      district: "",
      lat: 13.303005227138778,
      long: 103.9796463766434,
    },
    {
      district: "",
      lat: 13.440442720250852,
      long: 103.71973510206098,
    },
    {
      district: "",
      lat: 13.232399982258997,
      long: 104.33684532302983,
    },
    {
      district: "",
      lat: 13.698615549661712,
      long: 104.21744370698126,
    },
    {
      district: "",
      lat: 13.483410469370508,
      long: 103.87412317588966,
    },
    {
      district: "",
      lat: 13.364307627357189,
      long: 103.85798154316932,
    },
    {
      district: "",
      lat: 12.67868906722332,
      long: 105.08204096969567,
    },
  ];

  const [countryInfo, setCountryInfo] = useState({});
  const [countries, setCountries] = useState([]);
  const [mapCountries, setMapCountries] = useState([]);
  const [tableData, setTableData] = useState([]);
  const [casesType, setCasesType] = useState("cases");
  const [mapCenter, setMapCenter] = useState({ lat: 13.3633, lng: 103.8564 });
  const [mapZoom, setMapZoom] = useState(9);
  const [districtInfo, setDistrictInfo] = useState([]);
  const [sumSample, setSumSample] = useState({})

  //update the data
  const [district, setDistrict] = useState("");
  const [districtDatas, setDistrictDatas] = useState({});
  let [form] = Form.useForm();

  const { data: allProvince, loading, error, refetch } = useQuery(GET_ALL_PROVINCE, {
    variables: {
      district: district,
    },
    onCompleted: ({ getAllProvince }) => {
      console.log(getAllProvince)

      if (allData !== undefined) {
        let x = totalArray(allData?.allData)
        let y = getAllProvince

        // console.log(allData)
        // console.log(allData?.allData, "x")
        // console.log(y, "y")

        let sum = {};

        Object.keys(x)?.forEach(key => {
          if (y.hasOwnProperty(key)) {
            sum[key] = x[key] + y[key]
          }
        })

        setDistrictDatas(sum)
      }

      // if(district !== ""){
      //   let x = totalArray(allData?.allData.filter(ele => ele.district === district))
      //   let y = getAllProvince

      //   let sum = {};

      //   Object.keys(x).forEach(key => {
      //     if (y.hasOwnProperty(key)) {
      //       sum[key] = x[key] + y[key]
      //     }  
      //   })

      //   setDistrictDatas(sum)
      // }else{
      //   if(allData !== undefined){
      //     let x = totalArray(allData?.allData)
      //     let y = getAllProvince

      //     // console.log(allData?.allData, "x")
      //     // console.log(y, "y")

      //     let sum = {};

      //     Object.keys(x)?.forEach(key => {
      //       if (y.hasOwnProperty(key)) {
      //         sum[key] = x[key] + y[key]
      //       }  
      //     })

      //     setDistrictDatas(sum)
      //   } else {

      //     setDistrictDatas(getAllProvince);

      //   }
      // }
    },
  });

  useEffect(() => {

    refetch()

  }, [allProvince])

  const { data: dd } = useQuery(GET_DATA_FOR_MAP, {
    onCompleted: ({ getAllDistrictForMap }) => {
      // console.log(getAllDistrictForMap, "ddd")
      const mapDatas = joinArray(getAllDistrictForMap, districtLatLong);
      setDistrictInfo(mapDatas);
      let sortedData = sortData(mapDatas);
      setTableData(sortedData);
    },
  });

  useEffect(() => {
    if (roles?.includes("SHOP")) {
      history.push("/shop")
    }
  }, [])

  React.useEffect(() => {
    refetch();
  }, [district]);

  const { data: testPeople, loading: loadingTestPeople } = useQuery(GET_SUM_SAMPLE, {
    onCompleted: ({ getAllAllSampleTest }) => {
      // console.log(getAllAllSampleTest)
      setSumSample(getAllAllSampleTest);
    },
  });

  const { data: reportAdditional, loading: loadingAdditional } = useQuery(GET_REPORT_DATA_ADDITIONAL, {
    onCompleted: ({ allAdditional }) => {
      // setReportDataByAdditional(allAdditional)
    }
  })
  const { data: allData, loading: loadingAllData } = useQuery(GET_ALL_DATA, {
    onCompleted: ({ allData }) => {
      // setReportDataByAdditional(allAdditional)
    }
  })

  useEffect(() => {
    if (testPeople) {
      setSumSample(testPeople?.getAllAllSampleTest);
    }
  }, [testPeople])

  useEffect(() => {
    if (dd !== undefined || reportAdditional !== undefined) {
      let x = joinArray(dd?.getAllDistrictForMap, districtLatLong)
      let y = reportAdditional?.allAdditional
      let r = []

      y?.map(load => {
        var index = x?.findIndex(ele => ele._id === load.district)
        if (index === -1) {
          r.push({
            ...load,
            _id: load.district,
            confirmedCase: (load.positive === null ? 0 : load.positive),
            confirmedCaseToday: (load.positiveToday === null ? 0 : load.positiveToday),
            death: (load.death === null ? 0 : load.death),
            deathToday: (load.deathToday === null ? 0 : load.deathToday),
            recovered: (load.recover === null ? 0 : load.recover),
            recoveredToday: (load.recoverToday === null ? 0 : load.recoverToday),
          })
        } else {
          r.push({
            ...x[index],
            confirmedCase: x[index].confirmedCase + (load.positive === null ? 0 : load.positive),
            confirmedCaseToday: x[index].confirmedCaseToday + (load.positiveToday === null ? 0 : load.positiveToday),
            death: x[index].death + (load.death === null ? 0 : load.death),
            deathToday: x[index].deathToday + (load.deathToday === null ? 0 : load.deathToday),
            recovered: x[index].recovered + (load.recover === null ? 0 : load.recover),
            recoveredToday: x[index].recoveredToday + (load.recoverToday === null ? 0 : load.recoverToday),
          })
        }
      })

      x?.map(load => {
        var index = y?.findIndex(ele => ele.district === load._id)
        if (index === -1) {
          r.push(load)
        }
      })

      const mapDatas = joinArray(r, districtLatLong);

      let sortedData = sortData(mapDatas);
      setTableData(sortedData);
    }
  }, [reportAdditional, dd])

  useEffect(() => {
    if (allData !== undefined) {
      let x = totalArray(allData?.allData)
      let y = allProvince?.getAllProvince

      // console.log(x, "x")
      // console.log(y, "y")

      let sum = {};

      Object.keys(x)?.forEach(key => {
        if (y?.hasOwnProperty(key)) {
          sum[key] = x[key] + y[key]
        }
      })

      setDistrictDatas(sum)

      // console.log(sum)
    }
  }, [allData])

  const setToDistrictFn = (e) => {
    form.setFieldsValue({
      district: e,
      commune: null,
      village: null,
    });
    // setCommune("")

    if (e === "") {
      setDistrict("")
    } else {
      setDistrict(e);
    }

  };


  function lower(obj) {
    for (var prop in obj) {
      if (typeof obj[prop] === "string") {
        obj[prop] = obj[prop].toLowerCase();
      }
      if (typeof obj[prop] === "object") {
        lower(obj[prop]);
      }
    }
    return obj;
  }

  countries.forEach((c) => {
    lower(c);
  });

  return (!loading && !loadingAdditional && !loadingTestPeople && !loadingAllData) ? <>
    <Row>
      <AddMoreData open={openModal} setOpen={setOpenModal} />
      <Col xs={24} md={17}>
        <Row className="app__header">
          <FormControl className="app__dropdown">
            <Form.Item
              style={{ marginBottom: 0 }}
              name="district"
            // rules={[
            //   { required: true, message: "Please input your username!" },
            // ]}
            >
              <ListSelect
                type={0}
                data={convertToDistrict(districForMap)}
                title="/"
                setValue={setToDistrictFn}
              />
            </Form.Item>
          </FormControl>
        </Row>
        {/* <div className="app__stats"> */}
        <Row >
          <Col xs={24} xl={{ span: 16 }} xxl={{ span: 6 }} >
            <Row>
              <Col xs={12} style={{ padding: 5 }}>
                <Popconfirm
                  title="?"
                  okText=""
                  cancelText=""
                  onConfirm={() => {
                    history.push({
                      pathname: "/people",
                      state: { status: "", date: null, covidType: "" }
                    })
                  }}
                  onCancel={() => {
                    history.push({
                      pathname: "/people",
                      state: { status: "", date: new Date() }, covidType: ""
                    })
                  }}
                >

                  <InfoBox
                    onClick={(e) => {
                      setCasesType("cases")
                    }}
                    title=""
                    isRed
                    active={casesType === "cases"}
                    cases={districtDatas.confirmedCaseToday + ", (" + numeral(allProvince?.getAllProvince?.confirmFilledToday).format("0") + ")"}
                    total={numeral(districtDatas.confirmedCase).format("0")}
                    ImageShow={""}
                  />
                </Popconfirm>
              </Col>
              <Col xs={{ span: 12 }} style={{ padding: 5 }}>
                <Popconfirm
                  title=" DELTA?"
                  okText=""
                  cancelText=""
                  onConfirm={() => {
                    history.push({
                      pathname: "/people",
                      state: { status: "", date: null, covidType: "DELTA" }
                    })
                  }}
                  onCancel={() => {
                    history.push({
                      pathname: "/people",
                      state: { status: "", date: new Date(), covidType: "DELTA" }
                    })
                  }}
                >
                  <InfoBox
                    onClick={(e) => {
                      // setCasesType("cases")
                    }}
                    title="DELTA"
                    isRed
                    // active={casesType === "cases"}
                    cases={districtDatas.deltaToday + ", (" + numeral(allProvince?.getAllProvince?.deltaConfirmFilledFromToday).format("0") + ")"}
                    total={numeral(districtDatas.delta).format("0")}
                    ImageShow={""}
                  />
                </Popconfirm>
              </Col>
            </Row>

          </Col>

          <Col xs={24} xl={{ span: 8 }} xxl={{ span: 3 }} style={{ padding: 5 }}>

            <Popconfirm
              title=" Omicron?"
              okText=""
              cancelText=""
              onConfirm={() => {
                history.push({
                  pathname: "/people",
                  state: { status: "", date: null, covidType: "OMICRON" }
                })
              }}
              onCancel={() => {
                history.push({
                  pathname: "/people",
                  state: { status: "", date: new Date(), covidType: "OMICRON" }
                })
              }}
            >
              <InfoBox
                onClick={(e) => {}
                  // setCasesType("recovered")
                }
                title="OMICRON"
                // active={casesType === "recovered"}
                cases={districtDatas.omicronToday + ", (" + numeral(allProvince?.getAllProvince?.omicronConfirmFilledFromToday).format("0") + ")"}
                total={numeral(districtDatas.omicron).format("0")}
                ImageShow={""}
              />
            </Popconfirm>

          </Col>

          <Col xs={24} xl={{ span: 12 }} xxl={{ span: 5 }} style={{ padding: 5 }}>

            <Popconfirm
              title="?"
              okText=""
              cancelText=""
              onConfirm={() => {
                history.push({
                  pathname: "/people",
                  state: { status: "", date: null }
                })
              }}
              onCancel={() => {
                history.push({
                  pathname: "/people",
                  state: { status: "", date: new Date() }
                })
              }}
            >
              <InfoBox
                onClick={(e) => setCasesType("recovered")}
                title=""
                active={casesType === "recovered"}
                cases={districtDatas.recoveredToday + ", (" + numeral(allProvince?.getAllProvince?.recoveredFilledToday).format("0") + ")"}
                total={numeral(districtDatas.recovered).format("0")}
                ImageShow={ImageRecover}
              />
            </Popconfirm>

          </Col>
          <Col xs={24} xl={{ span: 12 }} xxl={{ span: 5 }} style={{ padding: 5 }}>

            <Popconfirm
              title="?"
              okText=""
              cancelText=""
              onConfirm={() => {
                history.push({
                  pathname: "/people",
                  state: { status: "", date: null }
                })
              }}
              onCancel={() => {
                history.push({
                  pathname: "/people",
                  state: { status: "", date: new Date() }
                })
              }}
            >
              <InfoBox
                onClick={(e) => setCasesType("deaths")}
                title=""
                isRed
                active={casesType === "deaths"}
                cases={districtDatas.deathToday + ", (" + numeral(allProvince?.getAllProvince?.deathFilledFromToday).format("0") + ")"}
                total={numeral(districtDatas.death).format("0")}
                ImageShow={ImageDeath}
              />
            </Popconfirm>

          </Col>
          <Col xs={24} xl={{ span: 12 }} xxl={{ span: 5 }} style={{ padding: 5 }}>
            <InfoBox
              // onClick={(e) => setCasesType("active")}
              title=""
              isRed
              // active={casesType === "deaths"}
              // cases={numeral(districtDatas.confirmedCase - districtDatas.recovered).format("0")}
              // total={numeral(districtDatas.death).format("0")}
              // total={numeral(districtDatas.confirmedCase - districtDatas.recovered).format("0")}
              total={numeral(districtDatas.confirmedCase - (districtDatas.death + districtDatas.recovered)).format("0")}
              ImageShow={ImageCure}
            />
          </Col>
        {/* </Row>
        <Row> */}
          <Col xs={24} xl={{ span: 12 }} xxl={{ span: 6 }} style={{ padding: 5 }}>
            <Popconfirm
              title="?"
              okText=""
              cancelText=""
              onConfirm={() => {
                history.push({
                  pathname: "/sample",
                  state: { date: null }
                })
              }}
              onCancel={() => {
                history.push({
                  pathname: "/sample",
                  state: { date: new Date() }
                })
              }}
            >
              <InfoBox
                onClick={(e) => setCasesType("sampleTest")}
                title=""
                isBlue
                active={casesType === "sampleTest"}
                cases={districtDatas.sampleTestToday}
                total={numeral(districtDatas.sampleTest).format("0")}
                ImageShow={ImageDirect}
              />
            </Popconfirm>
          </Col>
          <Col xs={24} xl={{ span: 12 }} xxl={{ span: 6 }} style={{ padding: 5 }}>
            <Popconfirm
              title="?"
              okText=""
              cancelText=""
              onConfirm={() => {
                history.push({
                  pathname: "/people",
                  state: {
                    date: null
                  }
                })
              }}
              onCancel={() => {
                history.push({
                  pathname: "/people",
                  state: {
                    date: new Date()
                  }
                })
              }}
            >
              <InfoBox
                // onClick={(e) => setCasesType("recovered")}
                title=""
                // active={casesType === "affected"}
                cases={districtDatas.totalAffectedPeopleToday}
                total={numeral(districtDatas.totalAffectedPeople).format("0")}
                ImageShow={ImageNotDirect}
              />
            </Popconfirm>
          </Col>
          <Col xs={24} xl={{ span: 12 }} xxl={{ span: 6 }} style={{ padding: 5 }}>
            <Popconfirm
              title="?"
              okText=""
              cancelText=""
              onConfirm={() => {
                history.push({
                  pathname: "/specifylocation",
                  state: {
                    date: null,
                    status: "",
                  }
                })
              }}
              onCancel={() => {
                history.push({
                  pathname: "/specifylocation",
                  state: {
                    date: new Date(),
                    status: "",
                  }
                })
              }}
            >
              <InfoBox
                // onClick={(e) => setCasesType("deaths")}
                title=""
                isRed
                // active={casesType === "deaths"}
                cases={districtDatas.totalAffectedLocationToday}
                total={numeral(districtDatas.totalAffectedLocation).format("0")}
                ImageShow={ImageClose}
              />
            </Popconfirm>
          </Col>
          <Col xs={24} xl={{ span: 12 }} xxl={{ span: 6 }} style={{ padding: 5 }}>
            <Popconfirm
              title="?"
              okText=""
              cancelText=""
              onConfirm={() => {
                history.push({
                  pathname: "/specifylocation",
                  state: {
                    date: null,
                    status: true,
                  }
                })
              }}
              onCancel={() => {
                history.push({
                  pathname: "/specifylocation",
                  state: {
                    date: new Date(),
                    status: true,
                  }
                })
              }}
            >
              <InfoBox
                // onClick={(e) => setCasesType("active")}
                title=""
                isRed
                // active={casesType === "deaths"}
                cases={districtDatas.totalAffectedLocationClosedToday}
                // total={numeral(districtDatas.death).format("0")}
                total={numeral(districtDatas.totalAffectedLocationClosed).format("0")}
                ImageShow={ImageOpen}
              />
            </Popconfirm>
          </Col>
        </Row>

        {/* </div> */}

        <Map
          district={districtInfo}
          casesType={casesType}
          center={mapCenter}
          zoom={mapZoom}
        />
      </Col>
      <Col xs={24} md={{ span: 6, offset: 0 }} style={{ paddingLeft: 30 }}>
        {roles?.includes("ADMIN") &&
          <Button
            style={{
              width: "100%"
            }}
            onClick={() => setOpenModal(true)}
          >
            
          </Button>
        }
        <Card
          className="first-card"
          style={{ marginTop: "20px" }}
        >
          <CardContent >
            <div className="app__information">
              <h3 className="covid_table">/</h3>
              <Divider />
              <Table district={tableData} />
            </div>
          </CardContent>
        </Card>
        <Card className="sec-card" style={{ marginTop: "10px" }}>
          <CardContent>
            <h3></h3>
            <BarGraph />
          </CardContent>
        </Card>
        <Card className="third-card" style={{ marginTop: "10px" }}>
          <CardContent>
            {casesType === "recovered" ? (
              <h3> </h3>
            ) : null}
            {casesType === "deaths" ? <h3> </h3> : null}
            {casesType === "cases" ? <h3> </h3> : null}
            <LineGraph casesType={casesType} />
          </CardContent>
        </Card>

        {pages?.includes("/graph") &&
          <button
            className="btn-grad-pro"
            style={{
              width: "100%",
              marginTop: 15
            }}
            onClick={() => history.push('/graph')}
          >
            
          </button>
        }
      </Col>
    </Row>
    {/* graph three datasets  */}

    {/* <Divider /> */}
    {/* <div className="graph ">
      <h2 className="covid_table"></h2>
      <BarChart casesType={casesType} />
 

    </div> */}

  </>
    :
    <div className="overlay-loading-background">
      <LoadingOverlay
        active={true}
        // spinner={<BounceLoader />}
        spinner={true}
        text="..."
      >
        {/* <p>Some content or children or something.</p> */}
      </LoadingOverlay>
    </div>
};

export default MapScreen;
