import React, { useContext, useEffect, useState } from "react";
import {
  Modal,
  Form,
  Input,
  Row,
  Col,
  Button,
  Select,
  DatePicker,
  message,
  Divider
} from "antd";
import { ListSelect, SelectCovidType } from "../../../static/own-comp";
import { useMutation, useQuery } from "@apollo/client";
import { RECORD_SAMPLETEST } from "../../../graphql/people";
import moment from "moment";
import {
  convertPersonalToSelect,
  convertLocationToSelect,
} from "../../../function/fn";
import AddLocation from "../../location/modal/addLocation";
import AddPeople from "../../people/modal/addPeople";
import { SelectTestLocation } from "../../../static/own-comp";
import { SelectPeople } from "../../../static/own-comp";
import { GET_ALL_PERSONINFO_FOR_SAMPLE_ADD } from '../../../graphql/people'
const { Option } = Select;

export default function AddSampleTest({
  open,
  setOpen,
  peopleID,
  setRefetch,
  peopleData,
  locationAllData,
  isHospital
}) {
  let [form] = Form.useForm();

  // console.log(personalAllData,"ttt")

  const [openPersonal, setOpenPersonal] = useState(false);
  const [openLocation, setOpenLocation] = useState(false);

  const [locationId, setLocationId] = useState("");
  const [personalInfoId, setPersonalInfoId] = useState("");

  const [locationData, setLocationData] = useState({});
  const [personalInfoData, setPersonalInfoData] = useState({});

  const [positiveState, setPositiveState] = useState(false);

  const [selectLocationData, setSelectLocationData] = useState([])
  const [newLocation, setNewLocation] = useState("")

  const [page, setPage] = useState(1)
  const [limit, setLimit] = useState(200)
  const [keyword, setKeyword] = useState(isHospital === true ? peopleData?.lastName : "")
  const [personalAllData, setPersonalAllData] = useState([])

  const { data: personalInfo } = useQuery(GET_ALL_PERSONINFO_FOR_SAMPLE_ADD, {
    variables: {
      page: page,
      limit: limit,
      keyword: keyword,
      startDate: null,
      endDate: null,
      currentState: null
    },
    onCompleted: ({ getPersonalInfoWithPagination }) => {
      // console.log(getPersonalInfoWithPagination)
      setPersonalAllData(getPersonalInfoWithPagination?.personalInfos)
    }
  })

  useEffect(() => {

    if (personalInfo) {
      setPersonalAllData(personalInfo?.getPersonalInfoWithPagination?.personalInfos)
      // console.log(personalAllData)
    }
    if (peopleData?.lastName !== undefined && isHospital === true) {
      setKeyword(peopleData?.lastName)
    }
  }, [keyword, personalInfo, peopleData])

  const [recordSampleTest, { loading }] = useMutation(RECORD_SAMPLETEST, {
    onCompleted: ({ recordSampleTest }) => {
      if (recordSampleTest.success === true) {
        setOpen(false);
        form.resetFields();

        message.success("");
        if (isHospital === true) {
          setRefetch();
        } else {
          setTimeout(() => window.location.reload(), 1000)
        }
      } else {
        message.error(recordSampleTest.message);
      }
    },
    onError: (error) => {
      message.error(error.message);
    }
  });

  const onFinish = (values) => {

    let newObj = {
      ...values,
      date: values.date === null || values.date === undefined ? null : moment.parseZone(values.date).format(),
      resultDate: values.resultDate === null || values.resultDate === undefined ? null : moment.parseZone(values.resultDate).format(),
      symptomStart: values.symptomStart === null || values.symptomStart === undefined ? null : moment.parseZone(values.symptomStart).format(),
      times: parseInt(values.times),
      personalInfoId: values.personalInfoId
    };

    if (personalInfoId === "new") {
      newObj = { ...newObj, personalInfoId: personalInfoData?.id }
    }
    recordSampleTest({
      variables: newObj,
    });

  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  const setPositiveStateFn = (e) => {
    form.setFieldsValue({
      result: e
    })
    setPositiveState(e)
  }

  const setToPersonalInfoFn = (e) => {
    form.setFieldsValue({
      personalInfoId: e,
    });
    if (e === "new") {
      setOpenPersonal(true);
    }

    setPersonalInfoId(e);
  };

  const callbackPersonalInfo = (e) => {

    if (e === "") {

      // setOpen(false)
      setPersonalInfoId(e);
      form.setFieldsValue({
        personalInfoId: null,
      });
    } else {
      setPersonalInfoData(e);

      form.setFieldsValue({
        personalInfoName: e.lastName + " " + e.firstName,
      });
    }
  };

  const setToLocationFn = (e) => {
    form.setFieldsValue({
      affectedLocationId: e,
    });
    if (e === "new") {
      setOpenLocation(true);
    }

    setLocationId(e);
  };

  const callbackLocation = (e) => {
    if (e === "") {
      setLocationId(e);
      form.setFieldsValue({
        affectedLocationId: null,
      });
    } else {
      setLocationData(e);
      form.setFieldsValue({
        affectedLocationName: e.affectedLocationName,
      });
    }
  };

  useEffect(() => {
    form.setFieldsValue({
      date: moment(),
      resultDate: moment(),
      testType: "RDT",
      result: false,
      reasonForTesting: "",
      specimentType: "",
      laboratory: "SRH",
      personalInfoId: isHospital === true ? peopleData?.personalInfoId : null,

    })

    console.log(peopleData)

  }, [peopleData])



  useEffect(() => {
    if (locationAllData !== undefined) {
      let newLocationAll = locationAllData.map(e => e._id).filter(e => e !== "").filter(e => e !== null)
      setSelectLocationData(newLocationAll)
    }
  }, [locationAllData])

  const setTestLocationFn = (e) => {

    form.setFieldsValue({
      testLocation: e,
    });

  };

  const setCovidVariantFn = (e) => {
    form.setFieldsValue({
      covidVariant: e,
    });
  };

  const addItem = () => {
    console.log('addItem');

    let isExist = selectLocationData.filter(e => e === newLocation)

    if (newLocation.trim() === "") {
      message.error("")
      setNewLocation("")
      return;
    }

    if (isExist.length > 0) {
      message.error("")
    } else {

      setSelectLocationData([...selectLocationData, newLocation])
      // form.setFieldsValue({
      //   testLocation: newLocation.toString(),
      // });
      setTestLocationFn(newLocation)
      setNewLocation("")
    }

  };

  //handle select people
  const handleSearchPeople = (e) => {
    if (e) {
      setKeyword(e)
    } else {
      // setPData([])
      setPersonalAllData([])
    }
  };


  return (
    <Modal
      title=""
      visible={open}
      onOk={() => setOpen(false)}
      onCancel={() => setOpen(false)}
      footer={null}
    >
      <AddPeople
        open={openPersonal}
        setOpen={setOpenPersonal}
        personalInfoId={personalInfoId}
        setPersonalInfoId={callbackPersonalInfo}
        personalInfoData={personalAllData}
      />
      <AddLocation
        open={openLocation}
        setOpen={setOpenLocation}
        locationId={locationId}
        setLocationId={callbackLocation}
      />
      <Form
        form={form}
        name="addPeopleTest"
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
      // fields={
      //   [
      //     {
      //       name:"date",
      //       value: moment(),
      //     },
      //     {
      //       name:"resultDate",
      //       value: moment(),
      //     },
      //     {
      //       name:"testType",
      //       value: "RDT",
      //     },
      //     {
      //       name:"result",
      //       value: false,
      //     },
      //     {
      //       name:"reasonForTesting",
      //       value:""
      //     },
      //     {
      //       name:"specimentType",
      //       value:""
      //     },
      //     {
      //       name:"laboratory",
      //       value:"SRH"
      //     }
      //   ]
      // }
      >
        <Row>
          {isHospital === true ?
            <Col
              xs={24}
              md={
                personalInfoId === "new"
                  ? { span: 11, offset: 0 }
                  : { span: 24, offset: 0 }
              }
            >
              <Form.Item
                name="personalInfoId"
                rules={[{ required: true, message: "!" }]}
              >
                <SelectPeople disabled data={convertPersonalToSelect(personalAllData)} title="" handleSearchPeople={handleSearchPeople} setValue={setToPersonalInfoFn} />
              </Form.Item>
            </Col>
            :
            <Col
              xs={24}
              md={
                personalInfoId === "new"
                  ? { span: 11, offset: 0 }
                  : { span: 24, offset: 0 }
              }
            >
              <Form.Item
                name="personalInfoId"
                rules={[{ required: true, message: "!" }]}
              >
                {/* <ListSelect
                type={6}
                data={convertPersonalToSelect(personalAllData)}
                title=""
                setValue={setToPersonalInfoFn}
              /> */}
                <SelectPeople data={convertPersonalToSelect(personalAllData)} title="" handleSearchPeople={handleSearchPeople} setValue={setToPersonalInfoFn} />
              </Form.Item>
            </Col>
          }

          {personalInfoId === "new" ? (
            <Col xs={24} md={{ span: 11, offset: 2 }}>
              <Form.Item name="personalInfoName">
                <Input
                  disabled={true}
                  style={{ backgroundColor: "white", color: "black" }}
                />
              </Form.Item>
            </Col>
          ) : null}

          <Col xs={24} md={{ span: 11, offset: 0 }}>
            <Form.Item
              name="date"
              rules={[{ required: true, message: "!" }]}
            >
              <DatePicker
                placeholder=""
                style={{ width: "100%" }}
              />
            </Form.Item>
          </Col>
          {/* 
          <Col xs={24} md={{ span: 11, offset: 0 }}>
            
          </Col> */}

          <Col xs={24} md={{ span: 11, offset: 2 }}>
            <Form.Item
              name="resultDate"
              rules={[{ required: true, message: "!" }]}
            >
              <DatePicker
                placeholder=""
                style={{ width: "100%" }}
              />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11, offset: 0 }}>

            <Form.Item
              name="testLocation"
              rules={[{ required: true, message: "!" }]}
            >
              <SelectTestLocation title="" data={selectLocationData} setNewValue={setNewLocation} newValue={newLocation} setValue={setTestLocationFn} addItem={addItem} />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11, offset: 2 }}>
            <Form.Item
              name="testType"
            // rules={[{ required: true, message: '!' }]}
            >
              <Select placeholder="" style={{ width: "100%" }}>
                <Option value="RDT">RDT</Option>
                <Option value="PCR">PCR</Option>
                <Option value=""></Option>
              </Select>
            </Form.Item>
          </Col>

          {/* <Col xs={24} md={ locationId === "new" ? { span: 11, offset: 0 } : { span: 24, offset: 0 }}>
                        <Form.Item
                            name="affectedLocationId"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={6} data={convertLocationToSelect(locationAllData)} title="" setValue={setToLocationFn} />
                        </Form.Item>
                    </Col>
                    {
                        locationId === "new" ? (
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="affectedLocationName"
                                >
                                    <Input disabled={true} style={{backgroundColor: "white", color: "black"}} />
                                </Form.Item>
                            </Col>
                        ) : null
                    } */}

          <Col xs={24} md={{ span: 11 }}>
            <Form.Item
              name="reasonForTesting"
              rules={[{ required: true, message: "!" }]}
            >
              {/* <Input placeholder="" /> */}
              <Select placeholder="" style={{ width: "100%" }}>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value="">
                  
                </Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11, offset: 2 }}>
            <Form.Item
              name="specimentType"
            // rules={[{ required: true, message: '!' }]}
            >
              {/* <Input placeholder="" /> */}
              <Select placeholder="" style={{ width: "100%" }}>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11, offset: 0 }}>
            <Form.Item
              name="laboratory"
            // rules={[{ required: true, message: '!' }]}
            >
              <Select placeholder="" style={{ width: "100%" }}>
                <Option value="IPC">IPC</Option>
                <Option value="NIPH">NIPH</Option>
                <Option value="PKML">PKML</Option>
                <Option value="MKB">MKB</Option>
                <Option value="BTB">BTB</Option>
                <Option value="TKV">TKV</Option>
                <Option value="SRH">SRH</Option>
                <Option value="NBTC">NBTC</Option>
                <Option value="SHV">SHV</Option>
                <Option value="RHPUOK">RHPUOK</Option>
              </Select>
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11, offset: 2 }}>
            <Form.Item
              name="result"
              rules={[{ required: true, message: "!" }]}
            >
              {/* <Input placeholder="" /> */}
              <Select
                placeholder=""
                style={{ width: "100%" }}
                onChange={(e) => setPositiveStateFn(e)}
              >
                <Option value={true}></Option>
                <Option value={false}></Option>
              </Select>
            </Form.Item>
          </Col>

          {positiveState === true ? (
            <Col xs={24} md={{ span: 11, offset: 0 }}>
              <Form.Item
                name="covidVariant"
              >
                <SelectCovidType setValue={setCovidVariantFn} />
              </Form.Item>
            </Col>
          ) : null}

          <Col
            xs={24}
            md={positiveState === true ? { span: 11, offset: 2 } : { span: 11, offset: 0 }}
          >
            <Form.Item
              name="times"
              rules={[{ required: true, message: "!" }]}
            >
              <Input type="number" placeholder="" />
            </Form.Item>
          </Col>

          <Col xs={24} md={positiveState === true ? { span: 24 } : { span: 11, offset: 2 }}>
            <Form.Item
              name="symptomStart"
            // rules={[{ required: true, message: '!' }]}
            >
              <DatePicker
                placeholder=""
                style={{ width: "100%" }}
              />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 24 }}>
            <Form.Item
              name="symptom"
            // rules={[{ required: true, message: '!' }]}
            >
              <Input placeholder="" />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 24 }}>
            <Form.Item name="other">
              <Input placeholder="" />
            </Form.Item>
          </Col>

          <Divider></Divider>

          <Col xs={24} md={{ span: 24 }}>
            <Form.Item
              name="labFormCompletedBy"
            // rules={[{ required: true, message: '!' }]}
            >
              <Input placeholder="" />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 24 }}>
            <Form.Item
              name="labFormCompletedByTel"
            // rules={[{ required: true, message: '!' }]}
            >
              <Input placeholder="" type="number" addonBefore="+855" />
            </Form.Item>
          </Col>

          <Col xs={24}>
            <Button htmlType="submit" type="primary" style={{ width: "100%" }}>
              
            </Button>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
}
