import React, {useContext, useState, useEffect} from 'react'
import { Row, Col, Button, Input, Table,message,Card,Form,DatePicker,Select, Switch } from 'antd'
import { PlusOutlined } from '@ant-design/icons';
import { hospitalCol, schoolCol } from '../component/school/tableColumn/schoolColumn'
import AddHospital from '../component/hospital/modal/addHospital';
import { useQuery,useMutation, useLazyQuery } from '@apollo/client';
import { GET_ALL_HOSPITAL,ALL_HOSPIAL_INFO } from '../graphql/hospital';

import { DELETE_HOSPITALINFO_BY_ID } from '../graphql/hospital';
import CustomMap from '../component/covideComponents/CustomMap';
import AddSchool from '../component/school/modal/addSchool';
import { schooldashboardCol } from '../component/schooldashboard/tableColumn/schooldashboardColumn';
import { schoolLeftCol } from '../component/schooldashboard/tableColumn/schoolLeftColumn';
import SchoolBarChart from '../component/schooldashboard/chart/schoolBarChart';
import { qrcodeDashboardCol } from '../component/qrcodedashboard/tableColumn/qrcodeDashBoardColumn';
import moment from 'moment';
import ExportToExcel from '../component/export/exportToExcel';
import { PeopleList, QrCodeHeader, TotalQrCodeHeader } from '../asset/exportExcel/reportExcel';
import { DELETE_TRANSACTION_BY_ID, GET_DATA_TOTAL_BOXES, GET_MARKET_WITH_SCAN, GET_TRANSACTION_FOR_GRAPH, GET_TRANSACTION_WITH_PAGINATION } from '../graphql/qrcodedashboard';
import QrCodeBarChart from '../component/qrcodedashboard/chart/qrCodeBarChart';
import FilterQRGraph from '../component/qrcodedashboard/modal/filterQRGraph';
import { qrCodeLeftCol } from '../component/qrcodedashboard/tableColumn/qrcodeLeftColumn';
import TotalBoxChart from '../component/qrcodedashboard/chart/totalBoxChart';
import { deathFilterCol } from '../component/deathfilter/tableColumn/deathFilterColumn';
import { GET_ALL_PERSONINFO } from '../graphql/people';
import DeathFilterChart from '../component/deathfilter/chart/DeathFilterChart';
import { GET_DATA_FOR_BARCHART, GET_GRAPH_AGE } from '../graphql/filterchart';
import { splitDot } from '../function/fn';
import LoadingOverlay from 'react-loading-overlay';
import { GET_DEATH_BY_VACCINATION } from '../graphql/death';

const {RangePicker} = DatePicker
const {Option} = Select

export default function DeathFilter() {

    const [qrCodeData, setQRCodeData] = useState([])
    let [form] = Form.useForm()

    const [openFilterGraph,setOpenFilterGraph] = useState(false)
    const [chartData,setChartData] = useState({})

    const [boxData,setBoxData] = useState([])
    const [peopleData, setPeopleData] = useState([])
    const [districtChartData,setDistrictChartData] =useState([])

    const [chartType, setChartType] = useState("ageBar")

    const [ageGrapData,setAgeGraphData] = useState([])

    const [ranges, setRanges] = useState({
        startDate: null,
        endDate: null,
    })   
    const [page, setPage] = useState(1)
    const [limit, setLimit] = useState(15)
    const [keyword, setKeyword] = useState("")
    const [filterCurrentState, setCurrentState] = useState(null)
    const [covidType,setCovidType] = useState("")

    const [totalDeath,setTotalDeath] = useState(0)

    const [filterData,setFilterData] = useState({
        keyword:"",
        createStartDate:null,
        createEndDate: null,
        marketName:"",
        limit:limit
    })

    var someDate = new Date();
    var numberOfDaysToAdd = 15;
    someDate.setDate(someDate.getDate() - numberOfDaysToAdd); 

    const {data,loading,error,refetch} = useQuery(GET_ALL_PERSONINFO,{
        variables:{
            page:page,
            limit:limit,
            keyword:keyword,
            currentState:"",
            startDate:ranges.startDate,
            endDate:ranges.endDate,
            covidType:covidType
        },
        onCompleted:({getPersonalInfoWithPagination})=>{
            setPeopleData(getPersonalInfoWithPagination)
        },
        fetchPolicy:'network-only',
        nextFetchPolicy:"cache-only",
    })

    useEffect(() => {
        refetch()
    }, [page,limit,keyword,filterCurrentState,covidType,ranges])

    useEffect(()=>{
        if(data!==undefined){
            setPeopleData(data?.getPersonalInfoWithPagination)
        }
    },[data])

    //chart data age
    const { data:ageData,loading:loadingAge,refetch:refetchAge } = useQuery(GET_GRAPH_AGE,{
        variables:{
            startDate:ranges.startDate,
            endDate:ranges.endDate
        },
        onCompleted:({getGraphByage})=>{
            console.log(getGraphByage)
            setAgeGraphData(getGraphByage)
        }
    })

    const { data:ageTotal } = useQuery(GET_GRAPH_AGE,{
        variables:{
            startDate:null,
            endDate:null
        },
        onCompleted:({getGraphByage})=>{
            let sum = 0;
            getGraphByage?.map(record=>{
                sum+=record.death
            })
            setTotalDeath(sum)
        }
    })

    useEffect(()=>{
    if(ageData!==undefined){
        //  console.log(abc)
        setAgeGraphData(ageData?.getGraphByage)
    }
    },[ageData])

    //chart data district
    const [getDataForBarChart,{ data:districtChart,loading:districtLoading,refetch:districtRefetch }] = useLazyQuery(GET_DATA_FOR_BARCHART)

    useEffect(()=>{
    if(districtChart!==undefined){
        //  console.log(districtChart)
        setDistrictChartData(districtChart?.getDataForBarChart)
    }
    },[districtChart])

    const handleSetDistrictChart =(e)=>{
        if(e){
            getDataForBarChart({
                variables:{
                    district:null,
                    commune:false,
                    village:false,
                    startDate:ranges.startDate,
                    endDate:ranges.endDate
                },
            })

            setChartType("districtBar")
        }else{
            setChartType("")
        }
        
    }

    const handleSetAgeChart = (e)=>{
        
        if(e){
            setChartType("ageBar")
        }else{
            setChartType("")
        }
    }


    const {data:box,loading:boxLoading,refetch:refetchBoxes} = useQuery(GET_DEATH_BY_VACCINATION,{
        onCompleted:({getDeathDataByVaccination})=>{
            console.log(getDeathDataByVaccination)
            setBoxData(getDeathDataByVaccination)
        }
    })

    useEffect(()=>{
        refetchBoxes()
    },[])

    // const {data:market,loading:marketLoading} = useQuery(GET_MARKET_WITH_SCAN,{
    //     onCompleted:({getMarketWithTotalScan})=>{
    //         setMarketData(getMarketWithTotalScan)
    //     }
    // })


    const onSelectDate = (e) => {
        if(e !== null){
            let startDate = moment(e[0])
            let endDate = moment(e[1])

            setRanges({
                startDate: startDate,
                endDate: endDate
            })
        }else{
            setRanges({
                startDate: null,
                endDate: null
            })
        }
    }

    const dateHolder = ["", ""]

    const onFinish = (values) => {
        setFilterData({
            ...values,
            keyword:values?.keyword !==undefined ? values.keyword:"",
            marketName:values.marketName!== undefined ? values.marketName:"",
            createStartDate: values.createDate !==undefined && values.createDate !==null ? moment.parseZone(values.createDate[0]):null,
            createEndDate: values.createDate !==undefined && values.createDate !==null ? moment.parseZone(values.createDate[1]):null,
            limit:values.limit!==undefined ? limit:15
        })

        setLimit(values.limit!==undefined ? values.limit:15)

    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    //to csv header
    const headers = [
        { label: ".", key: "no" },
        { label: "", key: "name" },
        { label: "", key: "englishName" },
        { label: "", key: "age" },
        { label: "", key: "gender" },
        { label: "", key: "tel" },
        { label: "*", key: "province" },
        { label: "*", key: "district" },
        { label: "*", key: "commune" },
        { label: "*", key: "village" },
        { label: "", key: "patientId" },
        { label: "", key: "date"},
        { label: "", key: "confirm" },
        { label: "", key: "direct" },
        { label: "", key: "times" },
        { label: "", key: "vaccinated" },
        { label: "", key: "vaccineType" },
        { label: "", key: "nationality" },
    ];

    //get no for data
    let peopleDataWithNo = []
    let dataToExcel =[]
    peopleData?.personalInfos?.map((record,index) =>{

        let pageAdd = peopleData?.paginator?.currentPage > 1 ? ((peopleData?.paginator?.currentPage*limit)-limit)+1: 1;

        let setNoToPeopleData = {...record,no:index+pageAdd}

        let toExcelItem = {
            no:index+1,
            name: record.lastName+" "+record.firstName,
            englishName: record.englishName === null || record.englishName === undefined ? "" : record.englishName,
            age: record.age,
            gender:record.gender,
            tel: record.tel === null || record.tel === undefined ? "" : record.tel,
            province: record.province,
            district: record.district === "" ? "": record.district,
            commune: record.commune === "" ? "": record.commune,
            village: record.village === "" ? "": record.village,
            patientId: splitDot(record.patientId),
            direct: record. affectedFrom.direct ? "":"",
            times: "" +record.sampleTest.length ,
            vaccinated:record.vaccination.times > 0 ? "":"",
            vaccineType: record.vaccination.vaccineType === null || record.vaccination.vaccineType === undefined ? "": record.vaccination.vaccineType,
            nationality:record.nationality
        }

        record?.sampleTest?.map(item=>{
        toExcelItem = {
            ...toExcelItem,
            date: moment.parseZone(item.date).format("D/MM/YYYY") , 
            confirm: item.result ? "Positive":"Negative"
        }
        })

        dataToExcel.push(toExcelItem)

        peopleDataWithNo.push(setNoToPeopleData)

    })


    const showAll =()=>{
        form.resetFields()
        form.submit()
        
        // refetch()
    }

    const handleOpenFilter=()=>{
        setOpenFilterGraph(true)
    }

    return (!loadingAge ? <>
            <Row>
                {/* <Col xs={24} md={24}>
                    <Row style={{height:'100%'}}>
                        <Col xs={24} md={5} style={{padding:10}}>
                            <Select
                                onChange={(e)=>setMarketName(e)}
                                placeholder=""
                                style={{width:'100%'}}
                            >
                                <Option value="">----</Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                        </Col>
                    </Row>
                </Col> */}
                <Col xs={24} sm={24} md={24} lg={24} xl={14} style={{padding:10}}>
                    <Card className="card-school" title={null} style={{height:'100%'}}>

                        <Switch checkedChildren="" unCheckedChildren="" onChange={(e)=> handleSetAgeChart(e)} checked={chartType==="ageBar"}/>&emsp;
                        <Switch checkedChildren="" unCheckedChildren="" onChange={(e)=> handleSetDistrictChart(e)} checked={chartType==="districtBar"}/>

                        <DeathFilterChart 
                            data={
                                chartType === "ageBar" ? ageGrapData: districtChartData
                            } 
                            isBar={chartType}
                        />
                    </Card>
                </Col>
                <Col xs={24} sm={24} md={24} lg={24} xl={10} >
                    <Row style={{height:'100%'}}>
                        <Col xs={24} md={12} style={{padding:10}}>
                            <Card className="card-school" title={null} style={{height:'100%'}}>
                                <h2></h2>

                                <span style={{marginTop:10,fontSize:30}} >{totalDeath}</span> <br/>
                                {/* <span className="school-badge-red">Content</span> */}
                            </Card>
                        </Col>

                        { 
                        boxData?.map(record =>(
                            <Col key={record?.times} xs={24} md={12} style={{padding:10}}>
                                <Card className="card-school" title={null} style={{height:'100%'}}>
                                    <h2> <b>{record.times}</b> </h2>

                                    <span style={{marginTop:10,fontSize:30}} >{record.frequency}</span> <br/>
                                    {/* <span className="school-badge-red"> {boxData?.totalShopToday}</span> */}
                                </Card>
                            </Col>
                        ))
                        
                        }
                        
                        {/* <Col xs={24} md={12} style={{padding:10}}>
                            <Card className="card-school" title={null} style={{height:'100%'}}>
                                <h2></h2>

                                <span style={{marginTop:10,fontSize:30}} >{boxData?.totalBuyer}</span> <br/>
                                <span className="school-badge-red"> {boxData?.totalBuyerToday}</span>
                            </Card>
                        </Col>
                        <Col xs={24} md={12} style={{padding:10}}>
                            <Card className="card-school" title={null} style={{height:'100%'}}>
                                <h2></h2>

                                <span style={{marginTop:10,fontSize:30}} >{boxData?.totalTransaction}</span> <br/>
                                <span className="school-badge-red"> {boxData?.totalTransactionToday}</span>
                            </Card>
                        </Col> */}
                        
                    </Row>
                </Col>
                
            </Row>
            <Row>
                
                <Col xs={24} md={24} >
                    <Row>
                
                        <Col xs={24} md={24} >
                            <Form
                            form={form}
                            name="search"
                            onFinish={onFinish}
                            onFinishFailed={onFinishFailed}
                            style={{width:'100%'}}
                        
                            >
                                <Row>
                                    <Col xs={24} md={{span:4}} style={{padding:5}}>

                                        <Form.Item>
                                            <ExportToExcel 

                                                headers={headers} 
                                                data={
                                                    dataToExcel
                                                } 
                                                filename={"DeathList"} 
                                                title={"Export to CSV"}
                                            />
                                        </Form.Item>
                                    </Col>

                                    <Col xs={24} md={{span:4}} style={{padding:5}}>
                                        <Form.Item name="limit">
                                            <Select placeholder="" style={{width:"100%"}} >
                                                <Option value={15}>15</Option>
                                                <Option value={100}>100</Option>
                                                <Option value={200}>200</Option>
                                                <Option value={500}>500</Option>
                                                <Option value={1000}>1000</Option>
                                                <Option value={1000}>2000</Option>
                                                <Option value={1000}>5000</Option>
                                                <Option value={10000}>10000</Option>
                                            </Select>
                                        </Form.Item>

                                    </Col>

                                    {/* <Col 
                                        xs={24}
                                        md={{span:4}}
                                        style={{padding:5}}
                                        >
                                        <Form.Item
                                            name="createDate"
                                            // rules={[{ required: true, message: '!' }]}
                                        >
                                            <RangePicker 
                                                // onChange={(e) => onSelectDate(e)}  
                                            
                                                placeholder={dateHolder}
                                                style={{width:"100%"}} 
                                            />
                                        </Form.Item>
                                    </Col> */}

                                    <Col xs={24} md={{span:4}} style={{padding:5}}>
                                        <Form.Item >
                                            <Button htmlType="submit" type="primary" style={{ width: "100%" }}>
                                            
                                            </Button>
                                        </Form.Item>
                                        
                                    </Col>
                                    <Col xs={24} md={{span:4}} style={{padding:5}}>
                                        <Form.Item >
                                            <Button onClick={()=> showAll()} type="primary" style={{ width: "100%" }}>
                                                
                                            </Button>
                                        </Form.Item>
                                    </Col>
                                    
                                </Row>
                            </Form>
                        </Col>

                        <Col xs={24} md={24} style={{padding:10}}>
                            <Table
                                // caseCol({handleDelete})
                                // style={{width:'100%'}}
                                className="death-table"
                                columns={deathFilterCol()}
                                dataSource={peopleDataWithNo}
                                rowKey={record => record.id}
                                pagination={{
                                    total: peopleData?.paginator?.totalDocs,
                                    //pageSizeOptions:["10", "20"],
                                    // showSizeChanger: true,
                                    // current: schoolData?.paginator?.currentPage,
                                    pageSize:limit,
                                    onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize) })
                                }}
                                scroll={{ x: 900 }} 
                                sticky 
                                loading={loading}
                            />
                        </Col>
                    </Row>
                </Col>
            </Row>
            
        </>
        :
        <>
            <div className="overlay-loading-background">
                    <LoadingOverlay
                        active={true}
                        // spinner={<BounceLoader />}
                        spinner={true}
                        text="..."
                    >
                    {/* <p>Some content or children or something.</p> */}
                    </LoadingOverlay>
            </div>
        </>
    )
}
